@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #060608;
  color: #E7DFDD;
  font-family: "Inter", Helvetica, Arial, sans-serif;
  margin: 0 1rem;
  padding: 0;
}

@media (max-width: 1020px) {
  body, a {
    font-size: 93.75%;
  }
}

@media (max-width: 720px) {
  body, a {
    font-size: 87.5%;
  }
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #E7DFDD;
}

header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-size: 1.2rem;
  padding: 20px;
  margin-bottom: 30px;
}

nav {
  padding: 10px;
  margin-right: 1rem;
}

nav a {
  margin: 1rem;
}

.title-page {
  text-align: center;
}

.home-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.info-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: 30rem;
}

.info-container span {
  color: #236bbe;
  font-size: 16px;
  font-weight: 900;
}

.home-container h1 {
  color: #236bbe;
  font-size: 2rem;
  margin: 0;
}

.home-container h2 {
  -webkit-align-items:baseline;
          align-items:baseline;
  display: -webkit-flex;
  display: flex;
  margin-bottom: 50px;
  margin-left: 5px;
}

.home-container button {
  background-color:#236bbe;
  border: none;
  border-radius: 5px;
  color: aliceblue;
  font-size: 1.2rem;
  font-weight: 900;
  padding: 10px 20px;
  margin: 0.5rem;
}
.home-container button:hover {
  background-color: blueviolet;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.5s;
}

.linha-vertical {
  border: 2px solid #236bbe;/* Adiciona borda esquerda na div como ser fosse uma linha.*/
  height: 18px;/*Altura da linha*/
  margin-right: 4px;
}

@media (max-width: 820px) {
  .image-home {
    display: none;
  }
}
.list-project-container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 70vh;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
  max-width: 70rem;
  padding: 0;
}

.btn-list {
  -webkit-align-items: center;
          align-items: center;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  color: aliceblue;
  display: -webkit-flex;
  display: flex;
  font-size: 5rem;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
}

.btn-list:focus {
  outline: none;
}

.btn-list:hover {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  transition: 0.5s;
}

.project-container {
  -webkit-align-items: center;
          align-items: center;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
}

.project-image {
  width: 100%;
}

.project-image img{
  border-radius: 10px 10px 10px 10px;
  width: 100%;
}

.project-image:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  transition: 0.5s;
}

.project-info {
  text-align: center;
  padding: 0 15px;
}

@media (max-width: 820px) {
  .list-project-container {
    height: 50vh;
    margin: 0;
  }

  .project-container {
    -webkit-align-items: center;
            align-items: center;
    border-radius: 10px;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    height: 100%;
    width: 100%;
  }
  .project-info {
    text-align: center;
    padding: 0;
    width: 100%;
  }

  .btn-list {
    font-size: 1rem;
    margin: 0;
    position: -webkit-sticky;
    position: sticky;
  }
} 
.stack-list-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height: auto;
  max-width: 1000px;
}

.stack-container h1 {
  color: #236bbe;
  margin: 1rem;
}

.stack-list-container li {
  display: block;
  font-size: 2rem;
  padding: 20px;
  width: 50%;
}

.stack-list-container span {
  margin-right: 1rem;
}

.stack-list-container li:nth-child(2) span {
  color: #e94e31;
}

.stack-list-container li:nth-child(3) span {
  color: #f06529;
}

.stack-list-container li:nth-child(4) span {
  color: #1e90ff;
}

.stack-list-container li:nth-child(5) span {
  color: #f7df1e;
}

.stack-list-container li:nth-child(6) span {
  color: #61dafb;
}

.stack-list-container li:nth-child(7) span {
  color: #764abc;
}

.stack-list-container li:nth-child(8) span {
  color: #1b5310;
}

.stack-list-container li:nth-child(9) span {
  color: #bf3a13;
}

.stack-list-container li:nth-child(10) span {
  color: #e1d3c8;
}

@media (max-width: 720px) {
  .stack-list-container {
    padding: 0;
  }

  .stack-list-container li{
    padding: 10px;
    width: 100%;
  }
}
